#hobbiesTitle {
    font-size: 30px;
    font-weight: bold;
    padding: 10px 100px 0 100px;
    text-align: left;
}
#hobbiesText {
    font-size: 25px;
    padding: 10px 100px 10px 100px;
    text-align: left;
}
#allHobbiesDiv {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
#eachHobbyDiv {
    border: 10px solid black;
    width: 200px;
    height: 200px;
    margin: 20px;
}
#eHTopLayerDiv {
    width: 100%;
    height: 100%;
    display: block;
}
#eHTopLayerDiv:hover {
    width: 200px;
    height: 200px;
    /* display: none; */
}
#eHBttmLayerDiv {
    width: 100%;
    height: 100%;
    z-index: 10;
    color: transparent;
    /* background-color: blue; */
    top: -100%;
    position: relative;
}
#eHBttmLayerDiv:hover {
    width: 100%;
    height: 100%;
    position: relative;
    text-align: center;

    color: black;
    text-shadow: 1px -1px 3px white, 
                -1px 1px 3px white, 
                -1px -1px 3px white, 
                 1px 1px 3px white;
    top: -100%;

}
.hobbyImg {
    height: 200px;
    width: 200px;
}
.hobbyName {
    position: relative;
    text-align: center;
    align-items: center;
    top: -90%;
    font-size: 20px;
    font-weight: bold;
    text-shadow: 1px -1px 3px white, 
                -1px 1px 3px white, 
                -1px -1px 3px white, 
                 1px 1px 3px white;
}
.hobbySkill {
    position: relative;
    text-align: center;
    align-items: center;
    font-size: 20px;
    bottom: -43%;
}
.hobbySkillText {
    position: relative;
    text-align: center;
    align-items: flex-end;
    font-size: 18px;
    bottom: -45%;
}